import { createSelector } from '@ngrx/store';

export const selectMandalaEditor = (state) => state.mandala.editor;

const getSelector = (prop) => {
    return createSelector(
        selectMandalaEditor,
        (state) => state[prop]
    );
};

export const zonesCount = getSelector('zonesCount');
export const size = getSelector('size');
export const isDrawMirrored = getSelector('isDrawMirrored');
export const tool = getSelector('tool');
export const strokeWidth = getSelector('strokeWidth');
export const smoothType = getSelector('smoothType');
export const color = getSelector('color');
export const editorState = selectMandalaEditor;
