import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { of, from } from 'rxjs';
import { mergeMap, take, debounceTime, catchError } from 'rxjs/operators';
import * as editorAction from '../actions/editor.actions';
import { IdbService, EditorService } from '../../services';

@Injectable()
export class EditorEffects {
    constructor(
        private actions$: Actions,
        private editorService: EditorService,
        private idbService: IdbService
    ) {}

    drawState$ = createEffect(() =>
        this.actions$.pipe(
            ofType(
                editorAction.color,
                editorAction.disableDrawMirrored,
                editorAction.enableDrawMirrored,
                editorAction.strokeWidth,
                editorAction.smoothType,
                editorAction.tool,
                editorAction.zonesCount,
                editorAction.addUserdDrawStep,
                editorAction.removeUserdDrawStep,
                editorAction.clearUserdDrawSteps,
                editorAction.updateCanvasSize
            ),
            mergeMap(() => {
                return this.editorService.getDrawState()
                    .pipe(
                        take(1),
                        debounceTime(100),
                        mergeMap((state) => {
                            return from(
                                !state.init ? of(null) : this.idbService.saveUserDrawState('current-state', state))
                                    .pipe(
                                        catchError(e => of(null))
                                    );
                        })
                    );
            })
        ), { dispatch: false }
    );

}
