import { Subject } from 'rxjs';
import { EditorService } from '../services';
import { takeUntil } from 'rxjs/operators';
import { TOOLS } from '../enums/tools';
import { SMOOTH_TYPES } from '../enums/smooth-types';

export abstract class EditorToolBase implements EditorTool {
    protected clear$: Subject<boolean>;
    protected color: string;
    protected zonesCount: number;
    protected isDrawMirrored: boolean;
    protected strokeWidth: number;
    protected smoothType: SMOOTH_TYPES;
    protected abstract tool: TOOLS;

    constructor(
        protected editorService: EditorService
    ) {}

    enable() {
        this.addSubscriptions();
    }

    disable() {
        this.clearSubscriptions();
    }

    addSubscriptions() {
        this.clear$ = new Subject();

        this.editorService.getColor().pipe(takeUntil(this.clear$))
            .subscribe(color => this.onChangeColor(color));

        this.editorService.getZonesCount().pipe(takeUntil(this.clear$))
            .subscribe(zonesCount => this.zonesCount = zonesCount);

        this.editorService.isDrawMirrored().pipe(takeUntil(this.clear$))
            .subscribe(isDrawMirrored => this.isDrawMirrored = isDrawMirrored);

        this.editorService.getStrokeWidth().pipe(takeUntil(this.clear$))
            .subscribe(strokeWidth => this.strokeWidth = strokeWidth);

        this.editorService.getSmoothType().pipe(takeUntil(this.clear$))
            .subscribe(smoothType => this.smoothType = smoothType);
    }

    clearSubscriptions() {
        this.clear$.next(true);
        this.clear$.unsubscribe();
        this.clear$ = null;
    }

    getState(points = [], toolParams = {}) {
        return {
            tool: {
                type: this.tool,
                params: {
                    ...toolParams
                }
            },
            color: this.color,
            zonesCount: this.zonesCount,
            isDrawMirrored: this.isDrawMirrored,
            strokeWidth: this.strokeWidth,
            smoothType: this.smoothType,
            points
        };
    }

    onChangeColor(color) {
        this.color = color;
    }
}
