import { createAction, props } from '@ngrx/store';
import { UserDrawStep, UserDrawTool } from '../reducers/editor.reducer';
import { SMOOTH_TYPES } from '../../enums/smooth-types';

export interface DrawPoint {
    x: number;
    y: number;
}

export interface DrawCanvasSize {
    width: number;
    height: number;
}


export const inited = createAction('[Mandala Editor] editor inited', props<{init: boolean}>());
export const zonesCount = createAction('[Mandala Editor] Zones Count', props<{zonesCount: number}>());
export const enableDrawMirrored = createAction('[Mandala Editor] Enable Draw Mirrored');
export const disableDrawMirrored = createAction('[Mandala Editor] Disable Draw Mirrored');
export const tool = createAction('[Mandala Editor] Tool', props<{tool: UserDrawTool}>());
export const strokeWidth = createAction('[Mandala Editor] Stroke Width', props<{strokeWidth: number}>());
export const smoothType = createAction('[Mandala Editor] Smooth Type', props<{smoothType: SMOOTH_TYPES}>());
export const color = createAction('[Mandala Editor] Color', props<{color: string}>());
export const addUserdDrawStep = createAction('[Mandala Editor] Add user draw step',  props<{ step: UserDrawStep, key: number}>());
export const removeUserdDrawStep = createAction('[Mandala Editor] Remove user draw step',  props<{ key: number}>());
export const clearUserdDrawSteps = createAction('[Mandala Editor] Clear user draw steps');
export const updateCanvasSize = createAction('[Mandala Editor] Update canvas size',  props<{ size: DrawCanvasSize}>());
