import { EditorDrawToolBase } from './draw.base';
import { EditorService, UndoService, CanvasService } from '../services';
import * as paper from 'paper';
import { TOOLS } from '../enums/tools';

export class DrawLineTool extends EditorDrawToolBase {
    protected tool = TOOLS.DRAW_LINE;
    protected toolParams;

    constructor(
        protected canvasService: CanvasService,
        protected editorService: EditorService,
        protected undoService: UndoService
    ) {
        super(editorService);
    }

    drawZoneStartIteration({ drawZonePoint }) {
        return this.createLine(drawZonePoint);
    }

    drawZoneIteration({ drawZonePath, drawZonePoint }) {
        const [, toSegment] = drawZonePath.segments;
        toSegment.point = drawZonePoint;
        return drawZonePath;
    }

    drawZoneEndIteration({ drawZonePath }) {
        drawZonePath.simplify();

        return drawZonePath.clone({insert: false});
    }

    createLine(position: paper.Point): paper.Path.Line {
        const line = new paper.Path.Line(position, position);
        line.strokeColor = new paper.Color(this.color);
        line.strokeWidth =  this.strokeWidth;
        line.strokeCap = 'round';

        return line;
    }
}
