import { EditorTapToolBase, TapPoint } from './tap.base';
import { EditorService, UndoService, CanvasService } from '../services';
import MagicWand from './../helpers/magic-wand.js';
import * as paper from 'paper';
import { TOOLS } from '../enums/tools';

export class FillBucketTool extends EditorTapToolBase {
    rasters = [];
    tool = TOOLS.FILL_BUCKET;

    constructor(
        protected canvasService: CanvasService,
        protected editorService: EditorService,
        protected undoService: UndoService
    ) {
        super(editorService);
    }

    tap({x, y}: TapPoint) {
        const cvs = this.canvasService.fillProject.view.element;
        const imageInfo = {
            data: null,
            width: cvs.width,
            height: cvs.height,
            context: cvs.getContext('2d')
        };

        const tempCanvas = document.createElement('canvas');
        const tempCtx = tempCanvas.getContext('2d');
        tempCtx.canvas.width = imageInfo.width;
        tempCtx.canvas.height = imageInfo.height;
        tempCtx.fillStyle = 'white';
        tempCtx.fillRect(0, 0, imageInfo.width, imageInfo.height);
        tempCtx.drawImage(cvs, 0, 0);
        imageInfo.data = tempCtx.getImageData(0, 0, imageInfo.width, imageInfo.height);

        const image = {
            data: imageInfo.data.data,
            width: imageInfo.width,
            height: imageInfo.height,
            bytes: 4
        };

        let mask = MagicWand.floodFill(image, x * 2, y * 2, 15);
        mask = MagicWand.gaussBlurOnlyBorder(mask, 5);

        const simplifyTolerant = 0;
        const simplifyCount = 30;
        let cs = MagicWand.traceContours(mask);
        cs = MagicWand.simplifyContours(cs, simplifyTolerant, simplifyCount);
        mask = null;

        const group = new paper.Group();
        // outer
        for (const csItem of cs) {
            if (csItem.inner) {
                continue;
            }

            let myPath = new paper.Path() as any;
            myPath.strokeColor = new paper.Color(this.color);
            myPath.fillColor = new paper.Color(this.color);
            const ps = csItem.points;
            myPath.position = new paper.Point(ps[0].x / 2, ps[0].y / 2);
            for (let j = 1; j < ps.length; j++) {
                myPath.add(new paper.Point(ps[j].x / 2, ps[j].y / 2));
            }
            myPath.closed = true;

            // inner
            for (const csItem2 of cs) {
                if (!csItem2.inner) {
                    continue;
                }
                const myPath2 = new paper.Path();
                myPath2.strokeColor = new paper.Color('red');
                myPath2.fillColor = new paper.Color('green');
                const ps2 = csItem2.points;
                myPath2.position = new paper.Point(ps2[0].x / 2, ps2[0].y / 2);
                for (let j = 1; j < ps2.length; j++) {
                    myPath2.add(new paper.Point(ps2[j].x / 2, ps2[j].y / 2));
                }
                myPath2.closed = true;

                const newPath = myPath.exclude(myPath2);
                myPath.remove();
                myPath2.remove();
                myPath = newPath;
            }

            myPath.remove();
            group.addChild(myPath);
        }

        const drawStep = this.getState([{x, y}]);
        const drawStepKey = this.editorService.addUserDrawStep(drawStep);
        this.canvasService.addPaperItems(drawStepKey, [group], [group]);
        this.undoService.addCommand({
            undo: () => {
                this.editorService.removeUserDrawStep(drawStepKey);
                this.canvasService.removePaperItems(drawStepKey);
            },
            redo: () => {
                this.editorService.addUserDrawStep(drawStep, drawStepKey);
                this.canvasService.addPaperItems(drawStepKey, [group], [group]);
            }
        });
    }

}
