import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import * as layoutActions from '../store/actions/layout.actions';
import * as layoutSelectors from '../store/selectors/layout.selectors';
import { StoreService } from './store.service';
import { Store } from '@ngrx/store';


@Injectable({
    providedIn: 'root'
})
export class LayoutService extends StoreService {
    constructor(
        store: Store<any>
    ) {
        super(store);
    }

    enableLoader() {
        this.dispatch(layoutActions.enableLoader());
    }

    disableLoader() {
        this.dispatch(layoutActions.disableLoader());
    }

    isEnabledLoader(): Observable<boolean> {
        return this.select(layoutSelectors.isEnabledLoader);
    }
}
