import { createReducer, on, Action } from '@ngrx/store';
import * as layoutActions from '../actions/layout.actions';

export interface LayoutState {
    isEnabledLoader: boolean;
}

const initState: LayoutState = {
    isEnabledLoader: false
};

const updateProp = (prop, status) => (state) => ({...state, [prop]: status});
const contentReducer = createReducer(
    initState,
    on(layoutActions.enableLoader, updateProp('isEnabledLoader', true)),
    on(layoutActions.disableLoader, updateProp('isEnabledLoader', false)),
);

export function reducer(state, action: Action) {
    return contentReducer(state, action);
}
