import { EditorTapToolBase, TapPoint } from './tap.base';
import { EditorService, UndoService, CanvasService } from '../services';
import { TOOLS } from '../enums/tools';

export class PipetteTool extends EditorTapToolBase {
    tool: TOOLS.PIPETTE;

    constructor(
        protected canvasService: CanvasService,
        protected editorService: EditorService,
        protected undoService: UndoService
    ) {
        super(editorService);
    }

    tap({x, y}: TapPoint) {
        const context = this.canvasService.getFillElement().getContext('2d');
        const pixel = context.getImageData(x * 2, y * 2, 1, 1);
        const rgb = pixel.data;
        this.editorService.setColor(`rgb(${rgb[0]}, ${rgb[1]}, ${rgb[2]})`);
    }
}
