import {Injectable, Inject, PLATFORM_ID} from '@angular/core';
import * as idb from 'idb';
import { isPlatformBrowser } from '@angular/common';
import { UserDrawState } from '../store/reducers/editor.reducer';


const IDB_USER_DRAW_KEY = 'user-draw';

@Injectable({
  providedIn: 'root'
})
export class IdbService {
    private dbPromise;
    private idb: any = idb;

    constructor(
        @Inject(PLATFORM_ID) platformId: string
    ) {
        if (isPlatformBrowser(platformId)) {
            this.connectToIDB();
        }
    }

    connectToIDB() {
        this.dbPromise = this.idb.openDB('mandala-app', 1.0, {
            upgrade: (db) => {
                if (!db.objectStoreNames.contains(IDB_USER_DRAW_KEY)) {
                    db.createObjectStore(IDB_USER_DRAW_KEY);
                }
            }
        });
    }

    saveUserDrawState(key: string, state: UserDrawState): Promise<any> {
        const saveState = {...state};
        delete saveState[`init`];
        return this.dbPromise.then((db: any) => {
            return db.put(IDB_USER_DRAW_KEY, saveState, key);
        });
    }

    getUserDrawState(key: string): Promise<UserDrawState> {
        return this.dbPromise.then((db: any) => {
            return db.get(IDB_USER_DRAW_KEY, key).then(res => res || {});
        });
    }

    clearUserDrawState(key: string): Promise<any> {
        return this.dbPromise.then((db: any) => {
            return db.delete(IDB_USER_DRAW_KEY, key);
        });
    }
}
