import { Component, OnInit, OnDestroy, Input, ViewChild, ElementRef } from '@angular/core';
import { EditorService, CanvasService, CanvasSize, UndoService } from '../../../services';
import { Subject, of, concat } from 'rxjs';
import { takeUntil, first, mergeMap, map, last, delay } from 'rxjs/operators';
import { ToolService } from '../../../services/tool.service';
import { EditorDrawToolBase } from '../../../tools/draw.base';
import { EditorTapToolBase } from '../../../tools/tap.base';
import { LayoutService } from 'src/app/modules/common/services';


@Component({
  selector: 'app-mandala-editor-canvas',
  templateUrl: './mandala-editor-canvas.component.html',
  styleUrls: ['./mandala-editor-canvas.component.scss']
})
export class MandalaEditorCanvasComponent implements OnInit, OnDestroy {
    @Input() initState;
    @ViewChild('drawCanvas', {static: true}) drawCanvas: ElementRef;
    @ViewChild('fillCanvas', {static: true}) fillCanvas: ElementRef;
    @ViewChild('zonesCanvas', {static: true}) zonesCanvas: ElementRef;
    destroy$ = new Subject<boolean>();
    tool: EditorTool;
    size: CanvasSize;

    get width() {
        return `${this.size.width}px`;
    }

    get height() {
        return `${this.size.height}px`;
    }

    constructor(
        private editorService: EditorService,
        private layoutService: LayoutService,
        private canvasService: CanvasService,
        private toolService: ToolService,
        private undoService: UndoService
    ) {}

    ngOnInit() {
        this.editorService.getSize().pipe(takeUntil(this.destroy$))
            .subscribe(size => this.onChangeEditorSize(size));

        this.canvasService.setZonesElement(this.zonesCanvas.nativeElement);
        this.canvasService.setFillElement(this.fillCanvas.nativeElement);
        this.canvasService.setDrawElement(this.drawCanvas.nativeElement);
        this.canvasService.updateCanvasesSize(this.size);

        this.layoutService.enableLoader();
        this.initDraw().pipe(takeUntil(this.destroy$))
            .subscribe(() => {
                this.undoService.clear();

                if (this.initState && this.initState.tool) {
                    const { tool, color, strokeWidth, zonesCount, isDrawMirrored, smoothType } = this.initState;
                    this.editorService.setTool(tool);
                    this.editorService.setColor(color);
                    this.editorService.setStrokeWidth(strokeWidth);
                    this.editorService.setZonesCount(zonesCount);
                    this.editorService.setSmoothType(smoothType);
                    if (isDrawMirrored) {
                        this.editorService.enableDrawMirrored();
                    } else {
                        this.editorService.disableDrawMirrored();
                    }
                }

                this.editorService.inited();

                this.editorService.getTool()
                    .pipe(
                        takeUntil(this.destroy$),
                        mergeMap(tool => this.toolService.createTool(tool))
                    ).subscribe((tool) => {
                        if (this.tool) {
                            this.tool.disable();
                        }

                        this.tool = tool;
                        this.toolService.setCurrentTool(tool);
                        tool.enable();
                    });

                this.layoutService.disableLoader();
            });

    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }

    onChangeEditorSize(size) {
        this.size = size;
        this.canvasService.updateCanvasesSize(this.size);
    }

    initDraw() {
        const drawSteps = [];
        const steps = this.initState.steps || {};
        for (const key in steps) {
            if (steps.hasOwnProperty(key)) {
                drawSteps.push(() => {
                    return of(null)
                    .pipe(
                        delay(50),
                        mergeMap(() => {
                            const step = steps[key];
                            this.editorService.setTool(step.tool);
                            this.editorService.setColor(step.color);
                            this.editorService.setStrokeWidth(step.strokeWidth);
                            this.editorService.setZonesCount(step.zonesCount);
                            this.editorService.setSmoothType(step.smoothType);
                            if (step.isDrawMirrored) {
                                this.editorService.enableDrawMirrored();
                            } else {
                                this.editorService.disableDrawMirrored();
                            }

                            return this.editorService.getTool()
                                .pipe(
                                    first(),
                                    mergeMap(tool => this.toolService.createTool(tool))
                                ).pipe(
                                    map((tool) => {
                                        if (this.tool) {
                                            this.tool.disable();
                                        }
                                        this.tool = tool;
                                        tool.enable();
                                        step.points.forEach((point, index) => {
                                            if (tool instanceof EditorDrawToolBase) {
                                                if (index === 0) {
                                                    tool.drawStart(point);
                                                } else {
                                                    tool.draw(point);
                                                }
                                            } else if (tool instanceof EditorTapToolBase) {
                                                tool.tap(point);
                                            }
                                        });

                                        if (tool instanceof EditorDrawToolBase) {
                                            tool.drawEnd();
                                        }

                                        if (step.transformPoints) {
                                            const drawTool = tool as EditorDrawToolBase;
                                            step.transformPoints.forEach((points = []) => {
                                                points.forEach((point, index) => {
                                                    if (index === 0) {
                                                        drawTool.transformStart(point);
                                                    } else {
                                                        drawTool.transform(point);
                                                    }
                                                });
                                                drawTool.transformEnd();
                                            });
                                        }

                                        if (tool instanceof EditorDrawToolBase) {
                                            tool.finishDraw();
                                        }

                                        return tool;
                                    })
                                );
                            })
                        );
                });
            }
        }

        return !drawSteps.length ? of(null) : concat(
            ...drawSteps.map(step => step())
        ).pipe(last());
    }
}
