import { createReducer, on, Action } from '@ngrx/store';
import * as editorActions from './../actions/editor.actions';
import { TOOLS } from '../../enums/tools';
import { SMOOTH_TYPES } from '../../enums/smooth-types';

export interface UserDrawState {
    init: boolean;
    zonesCount: number;
    isDrawMirrored: boolean;
    tool: {
        type: TOOLS;
        params?: {}
    };
    strokeWidth: number;
    smoothType: SMOOTH_TYPES;
    color: string;
    size: {width: number, height: number};
    steps: UserDrawStateStep;
}

export interface UserDrawStateStep {
    [key: number]: UserDrawStep;
}
export interface UserDrawTool {
    type: TOOLS;
    params?: {
        imagePath?: string
    };
}

export interface UserDrawStep {
    points: {x: number, y: number}[];
    zonesCount?: number;
    isDrawMirrored?: boolean;
    tool?: UserDrawTool;
    strokeWidth?: number;
    color?: string;
    smoothType?: SMOOTH_TYPES;
}

export const initialState: UserDrawState = {
    init: false,
    zonesCount: 10,
    isDrawMirrored: false,
    tool: {
        type: TOOLS.DRAW_FREE
    },
    strokeWidth: 6,
    smoothType: SMOOTH_TYPES.LIGHT_SMOOTH,
    color: 'rgb(0, 0, 0)',
    size: null,
    steps: {}
};

const updateState = (state, props) => ({...state, ...props});
const updatePropStatus = (prop: string, status: boolean) => (state) => ({...state, [prop]: status});
const addUserDrawStep = (state, {step, key}) => ({...state, steps: {...state.steps, [key]: step}});
const removeUserDrawStep = (state, {key}) => {
    const steps = {...state.steps};
    delete steps[key];
    return {...state, steps};
};

const transformReducer = createReducer(
    initialState,
    on(editorActions.zonesCount, updateState),
    on(editorActions.enableDrawMirrored, updatePropStatus('isDrawMirrored', true)),
    on(editorActions.disableDrawMirrored, updatePropStatus('isDrawMirrored', false)),
    on(editorActions.tool, updateState),
    on(editorActions.strokeWidth, updateState),
    on(editorActions.smoothType, updateState),
    on(editorActions.color, updateState),
    on(editorActions.addUserdDrawStep, addUserDrawStep),
    on(editorActions.removeUserdDrawStep, removeUserDrawStep),
    on(editorActions.clearUserdDrawSteps, state => ({...state, steps: []})),
    on(editorActions.updateCanvasSize, (state, {size}) => ({...state, size})),
    on(editorActions.inited, (state, {init}) => ({...state, init})),
);

export function reducer(state, action: Action) {
    return transformReducer(state, action);
}
