import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import * as paper from 'paper';

export interface CanvasSize {
    width: number;
    height: number;
}

export interface CanvasPoint {
    x: number;
    y: number;
}

@Injectable()
export class CanvasService {
    private size: CanvasSize;
    private drawElement: HTMLCanvasElement;
    private fillElement: HTMLCanvasElement;
    private cachedCanvasRect: ClientRect;
    private exportPaperGroups = new Map<string, any>();
    private cachedPaperGroups = new Map<string, any>();
    private cachedPaperRasterizedGroups = new Map<any, any>();
    fillProject: paper.Project;
    zonesProject: paper.Project;
    drawProject: paper.Project;
    backgroundRect: paper.Path.Rectangle;

    clear() {
        this.cachedPaperGroups.clear();
        this.cachedPaperRasterizedGroups.clear();
        this.exportPaperGroups.clear();
        this.fillProject.remove();
        this.drawProject.remove();
        this.zonesProject.remove();
        this.size = null;
        this.drawElement = null;
        this.fillElement = null;
        this.cachedCanvasRect = null;
        this.fillProject = null;
        this.drawProject = null;
        this.zonesProject = null;
    }

    setZonesElement(zonesElement: HTMLCanvasElement) {
        this.zonesProject = new paper.Project(zonesElement);
        this.zonesProject.view.autoUpdate = false;
    }


    setDrawElement(drawElement: HTMLCanvasElement) {
        this.drawElement = drawElement;
        this.drawProject = new paper.Project(drawElement);
        this.drawProject.view.autoUpdate = false;

        this.addBackgroundRect('white');
        this.drawProject.view.update();
    }

    getDrawElement() {
        return this.drawElement;
    }


    setFillElement(fillElement: HTMLCanvasElement) {
        this.fillElement = fillElement;
        this.fillProject = new paper.Project(fillElement);
        this.fillProject.view.autoUpdate = false;
    }

    getFillElement() {
        return this.fillElement;
    }


    getCanvasSize(): CanvasSize {
        return this.size;
    }

    updateCanvasesSize(size: CanvasSize) {
        this.size = size;
        if (size) {
            const canvases = [this.fillProject, this.drawProject, this.zonesProject];
            canvases.forEach((project) => {
                if (project) {
                    project.view.viewSize = new paper.Size(size.width, size.height);
                }
            });
        }
    }

    getCanvasRect(useCachedCanvasRect?: boolean): ClientRect {
        if (useCachedCanvasRect && this.cachedCanvasRect) {
            return this.cachedCanvasRect;
        }

        this.cachedCanvasRect = this.drawElement.getBoundingClientRect();
        return this.cachedCanvasRect;
    }


    normalizePoint(point: CanvasPoint, useCachedCanvasRect?: boolean): CanvasPoint {
        const rect = this.getCanvasRect(useCachedCanvasRect);
        return {
            x: point.x - rect.left,
            y: point.y - rect.top
        };
    }


    addPaperItems(drawStepKey, items, exportItems) {
        const group = new paper.Group(items);
        const rasterize = group.rasterize();
        group.remove();
        rasterize.remove();

        this.cachedPaperGroups.set(drawStepKey, group);
        this.cachedPaperRasterizedGroups.set(group, rasterize);

        this.fillProject.activeLayer.addChild(rasterize);
        this.fillProject.view.update();
        this.drawProject.view.update();

        const exportGroup = new paper.Group(exportItems);
        exportGroup.remove();
        this.exportPaperGroups.set(drawStepKey, exportGroup);
    }

    removePaperItems(drawStepKey) {
        const group = this.cachedPaperGroups.get(drawStepKey);
        const rasterize = this.cachedPaperRasterizedGroups.get(group);
        this.cachedPaperRasterizedGroups.delete(group);
        this.cachedPaperGroups.delete(drawStepKey);
        this.exportPaperGroups.delete(drawStepKey);

        group.remove();
        rasterize.remove();
        this.fillProject.view.update();
        this.drawProject.view.update();
    }

    getExportPaperGroups() {
        return this.exportPaperGroups;
    }


    addBackgroundRect(color) {
        const startRect = this.createPoint(0, 0);
        const rectSize = new paper.Size(this.size.width, this.size.height);
        const baseLayer = new paper.Layer();
        baseLayer.activate();
        this.backgroundRect = new paper.Path.Rectangle(startRect, rectSize);
        this.backgroundRect.fillColor = new paper.Color(color);

        const drawLayer = new paper.Layer();
        drawLayer.activate();

        setTimeout(() => this.drawProject.view.update());
    }


    private createPoint(x: number, y: number): paper.Point {
        return new paper.Point(x, y);
    }
}
