import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Store } from '@ngrx/store';

import * as editorActions from '../store/actions/editor.actions';
import * as editorSelectors from '../store/selectors/editor.selectors';
import { StoreService } from '../../common/services/store.service';
import { UserDrawStep, UserDrawTool } from '../store/reducers/editor.reducer';
import { SMOOTH_TYPES } from '../enums/smooth-types';

const MIN_ZONES_COUNT = 1;
const MAX_ZONES_COUNT  = 30;

@Injectable()
export class EditorService extends StoreService {
    constructor(
        store: Store<any>
    ) {
        super(store);
    }

    inited() {
        this.dispatch(editorActions.inited({ init: true }));
    }


    getDrawState() {
        return this.select(editorSelectors.editorState);
    }


    setZonesCount(count: number) {
        const zonesCount = Math.min(MAX_ZONES_COUNT, Math.max(MIN_ZONES_COUNT, count));
        this.dispatch(editorActions.zonesCount({zonesCount}));
    }

    getZonesCount(): Observable<number> {
        return this.select(editorSelectors.zonesCount);
    }


    setTool(tool: UserDrawTool) {
        this.dispatch(editorActions.tool({ tool }));
    }

    getTool(): Observable<UserDrawTool> {
        return this.select(editorSelectors.tool);
    }


    enableDrawMirrored() {
        this.dispatch(editorActions.enableDrawMirrored());
    }

    disableDrawMirrored() {
        this.dispatch(editorActions.disableDrawMirrored());
    }

    isDrawMirrored(): Observable<boolean> {
        return this.select(editorSelectors.isDrawMirrored);
    }


    setStrokeWidth(strokeWidth: number) {
        this.dispatch(editorActions.strokeWidth({ strokeWidth }));
    }

    getStrokeWidth(): Observable<number> {
        return this.select(editorSelectors.strokeWidth);
    }


    setSmoothType(smoothType: SMOOTH_TYPES) {
        this.dispatch(editorActions.smoothType({ smoothType }));
    }

    getSmoothType(): Observable<SMOOTH_TYPES> {
        return this.select(editorSelectors.smoothType);
    }


    setColor(color: string) {
        this.dispatch(editorActions.color({ color }));
    }

    getColor(): Observable<string> {
        return this.select(editorSelectors.color);
    }


    addUserDrawStep(step: UserDrawStep, key = Date.now()): number {
        this.dispatch(editorActions.addUserdDrawStep({ step, key }));

        return key;
    }

    removeUserDrawStep(key: number) {
        this.dispatch(editorActions.removeUserdDrawStep({ key }));
    }

    clearUserDrawSteps() {
        this.dispatch(editorActions.clearUserdDrawSteps());
    }


    updateSize(size: editorActions.DrawCanvasSize) {
        this.dispatch(editorActions.updateCanvasSize({ size }));
    }

    getSize(): Observable<editorActions.DrawCanvasSize> {
        return this.select(editorSelectors.size);
    }
}
