import { Injectable } from '@angular/core';
import { UndoStackItem, UndoStackItemCommand } from './undo-stack-item';
import { Subject, fromEvent } from 'rxjs';
import { Router, NavigationStart } from '@angular/router';
import { takeUntil, filter } from 'rxjs/operators';

@Injectable()
export class UndoService {
    destroy$ = new Subject<boolean>();
    stack: UndoStackItem[] = [];
    index = -1;
    limit = 50;
    isEnabled = false;

    constructor(
        private router: Router
    ) {}

    enable() {
        if (this.isEnabled) {
            return;
        }

        this.isEnabled = true;

        fromEvent(window, 'keydown').pipe(takeUntil(this.destroy$))
            .subscribe((e: KeyboardEvent) => this.onKeydown(e));

        this.router.events.pipe(filter(event => event instanceof NavigationStart))
            .subscribe((() => this.clear()));
    }

    disable() {
        this.destroy$.next(true);
        this.isEnabled = false;
    }

    hasUndo(): boolean {
        return this.index > -1;
    }

    hasRedo(): boolean {
        return this.index < (this.stack.length - 1);
    }

    add(item: UndoStackItem) {
        this.stack.splice(this.index + 1, this.stack.length - this.index);
        this.stack.push(item);

        if (this.stack.length > this.limit) {
            this.stack.splice(0, this.stack.length - this.limit);
        }

        this.index = this.stack.length - 1;
    }

    addCommand(command: UndoStackItemCommand): UndoStackItem {
        const undoStackItem = UndoStackItem.createUndoStackItem(command);
        this.add(undoStackItem);

        return undoStackItem;
    }

    undo() {
        const item = this.stack[this.index];
        if (item) {
            item.undo();
            this.index -= 1;
        }
    }

    redo() {
        const item = this.stack[this.index + 1];
        if (item) {
            item.redo();
            this.index += 1;
        }
    }

    remove(item: UndoStackItem) {
        const index = this.stack.indexOf(item);
        if (index > -1) {
            this.stack.splice(index, 1);
            if (this.index >= index) {
                this.index--;
            }
        }
    }

    setLimit(limit: number) {
        this.limit = limit;
    }

    clear() {
        this.stack = [];
        this.index = -1;
    }

    private onKeydown(event: KeyboardEvent) {
        const isCtrlZ = (e) => (e.metaKey || e.ctrlKey) && e.keyCode === 90;
        if (event.shiftKey && isCtrlZ(event)) {
            this.redo();
        } else if (isCtrlZ(event)) {
            this.undo();
        }
    }
}
