import { Injectable } from '@angular/core';
import { TOOLS, DRAW_IMAGES } from '../enums/tools';
import { EditorService } from './editor.service';
import { DrawFreeTool, DrawLineTool, FillBackgroundTool, PipetteTool, FillBucketTool, DrawImageTool } from '../tools';
import { UndoService } from './undo/undo.service';
import { CanvasService } from './canvas.service';
import { Observable, Observer } from 'rxjs';
import { UserDrawTool } from '../store/reducers/editor.reducer';
import { HttpClient, HttpHeaders } from '@angular/common/http';


@Injectable()
export class ToolService {
    private currentTool: EditorTool;

    constructor(
        private editorService: EditorService,
        private canvasService: CanvasService,
        private undoService: UndoService,
        private http: HttpClient
    ) {}

    createTool(tool: UserDrawTool): Observable<EditorTool> {
        return Observable.create((observer: Observer<EditorTool>) => {
            switch (tool.type) {
                case TOOLS.DRAW_FREE:
                    observer.next(new DrawFreeTool(this.canvasService, this.editorService, this.undoService));
                    break;
                case TOOLS.DRAW_LINE:
                    observer.next(new DrawLineTool(this.canvasService, this.editorService, this.undoService));
                    break;
                case TOOLS.DRAW_IMAGE:
                    const imagePath = tool.params && tool.params.imagePath ? tool.params.imagePath : DRAW_IMAGES[0];
                    const responseType = 'text';
                    const headers = new HttpHeaders();
                    headers.set('Accept', 'image/svg+xml');
                    this.http.get(imagePath, {headers, responseType})
                        .subscribe(data => {
                            const imageDrawTool = new DrawImageTool(this.canvasService, this.editorService, this.undoService);
                            imageDrawTool.setDrawImage(data, imagePath);
                            observer.next(imageDrawTool);
                            observer.complete();
                        });
                    return;
                case TOOLS.FILL_BACKGROUND:
                    observer.next(new FillBackgroundTool(this.canvasService, this.editorService, this.undoService));
                    break;
                case TOOLS.FILL_BUCKET:
                    observer.next(new FillBucketTool(this.canvasService, this.editorService, this.undoService));
                    break;
                case TOOLS.PIPETTE:
                    observer.next(new PipetteTool(this.canvasService, this.editorService, this.undoService));
                    break;
            }

            observer.complete();
        });
    }

    getCurrentTool(): EditorTool {
        return this.currentTool;
    }

    setCurrentTool(tool: EditorTool) {
        this.currentTool = tool;
    }
}
