import { EditorDrawToolBase } from './draw.base';
import { EditorService, UndoService, CanvasService } from '../services';
import * as paper from 'paper';
import { TOOLS } from '../enums/tools';
import { SMOOTH_TYPES } from '../enums/smooth-types';

export class DrawFreeTool extends EditorDrawToolBase {
    protected tool = TOOLS.DRAW_FREE;
    protected toolParams: any;

    constructor(
        protected canvasService: CanvasService,
        protected editorService: EditorService,
        protected undoService: UndoService
    ) {
        super(editorService);
    }

    drawZoneStartIteration({ drawZonePoint }) {
        return this.createPath(drawZonePoint);
    }

    drawZoneIteration({ drawZonePath, drawZonePoint }) {
        drawZonePath.add(drawZonePoint);

        return drawZonePath;
    }

    drawZoneEndIteration({ drawZonePath }) {
        if (this.smoothType === SMOOTH_TYPES.HARD_SMOOTH) {
            drawZonePath.simplify();
            drawZonePath.smooth();
        } else if (this.smoothType === SMOOTH_TYPES.LIGHT_SMOOTH) {
            drawZonePath.simplify();
        }

        return drawZonePath.clone({insert: false});
    }

    createPath(position: paper.Point): paper.Path {
        const path = new paper.Path();
        path.moveTo(position);
        path.strokeColor = new paper.Color(this.color);
        path.strokeWidth = this.strokeWidth;
        path.strokeCap = 'round';

        return path;
    }
}
