import { Component, ViewChild, ElementRef, AfterViewInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import iro from '@jaames/iro';
import { EditorService } from '../../../services';


@Component({
  selector: 'app-mandala-editor-colorpicker',
  templateUrl: './mandala-editor-colorpicker.component.html',
  styleUrls: ['./mandala-editor-colorpicker.component.scss']
})
export class MandalaEditorColorpickerComponent implements AfterViewInit, OnDestroy {
    @Input() color: string;
    @Output() changeColor = new EventEmitter<string>();
    @ViewChild('picker', {static: true}) picker: ElementRef;
    colorPicker: iro.ColorPicker;
    destroy$ = new Subject<boolean>();

    constructor(
        public editorService: EditorService
    ) {}

    ngAfterViewInit() {
        this.colorPicker = new iro.ColorPicker(this.picker.nativeElement, {
            width: 300,
            handleRadius: 12,
            sliderHeight: 30,
            color: this.color
        });

        this.colorPicker.on('color:change', color => this.changeColor.emit(color.rgbString));

        this.editorService.getColor().pipe(takeUntil(this.destroy$))
            .subscribe(color => this.onChangeColor(color));
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }

    onChangeColor(color: string) {
        if (this.colorPicker.color.rgbString !== color) {
            this.colorPicker.color.rgbString = color;
        }
    }
}
